async function loadDayDetails(dateStr) {
    console.log('[LOAD DAY DETAILS]    :', dateStr);
    
    try {
        const { data, error } = await supabase
            .from('day_details')
            .select('*')
            .eq('date', dateStr)
            .single();
            
        if (error) {
            console.error('[LOAD DAY DETAILS]    :', error);
            return {};
        }
        
        return data || {};
    } catch (error) {
        console.error('[LOAD DAY DETAILS]    :', error);
        return {};
    }
}

async function saveDayDetails(date, detailsToSave) {
    console.log('[SAVE DAY DETAILS]    :', date, detailsToSave);
    
    try {
        const { error } = await supabase
            .from('day_details')
            .upsert({
                date: date,
                ...detailsToSave
            });
            
        if (error) {
            throw error;
        }
        
        console.log('[SAVE DAY DETAILS]   ');
    } catch (error) {
        console.error('[SAVE DAY DETAILS]    :', error);
        throw error;
    }
}